import { FC } from 'react';

import { CardScope, CardProps } from './card.interface';

export const Card: FC<CardProps> = ({ children, image, ...props }) => {
  const padding = image ? 'pt-0 px-0 pb-zep_1_5' : 'px-zep-1_5 py-zep-2';
  return (
    <div
      className={`
        ${CardScope} 
        ${padding}
        h-full
        flex flex-col flex-nowrap
        focus-visible:outline-none
        rounded-lg bg-white border-zep-1 border-zep-lightgrey-500 shadow-sm hover:shadow-md
        [&>img]:rounded-tl-lg [&>img]:rounded-tr-lg
      `}
      tabIndex={0}
      data-testid="zep-card"
      {...props}
    >
      {image}
      {image ? <div className="px-zep-1_5 py-zep-2 flex flex-col flex-nowrap h-full">{children}</div> : children}
    </div>
  );
};
